
































import Vue from 'vue'
import { R4 } from '@Ahryman40k/ts-fhir-types'

import api from '@/api'

interface IData {
  text: string
  searched: boolean
  loading: boolean
  results: Array<R4.IMeasure>
}

// Using a global will persist the object across routes.
const DATA: IData = {
  text: '',
  searched: false,
  loading: false,
  results: []
}

export default Vue.extend({
  data (): IData {
    return DATA
  },

  methods: {
    async formSubmit () {
      this.loading = true
      this.searched = false

      const measures = await api.measure.search({ text: this.text })
      this.results = measures

      this.loading = false
      this.searched = true
    }
  }
})
